html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video, button {
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  border: none;
  margin: 0;
  padding: 0;
  font-family: Love Ya Like A Sister, cursive;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after, q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

button {
  cursor: pointer;
}

html, body, .wrap {
  cursor: url("../IMG/cookie4.png"), auto;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

body, .wrap, header, section, footer {
  margin: 0 auto;
}

body {
  background-color: #0f0d29;
  background-image: url("../back.e01f7aad.gif");
}

a {
  color: #000;
  text-decoration: none;
}

#background {
  z-index: 1;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  position: relative;
}

#background img {
  z-index: -3;
  width: 100%;
  position: absolute;
  top: 70%;
  left: 0;
}

.rotimg {
  transform-origin: center;
  width: 100vw;
  animation: 50s linear infinite rotation;
}

@keyframes rotation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(359deg);
  }
}

.wrapAll {
  z-index: 2;
  height: 100%;
  display: block;
}

header {
  padding: 24px 0;
}

section {
  width: 100%;
  height: 65%;
}

header .container {
  color: #ffffffbe;
}

header .container .text {
  padding-top: 2vh;
  font-size: 2rem;
}

header #siteName {
  text-align: right;
  color: #ffffff80;
  padding-right: 4vw;
  font-size: 1.1em;
}

header .container .text h3 {
  text-align: center;
  font-size: 150%;
  line-height: 8vh;
}

section .container {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

footer .container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 24px;
  display: flex;
}

footer .container ul {
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: 0 auto;
  padding: 24px;
  display: flex;
}

footer .container ul li {
  text-align: center;
  white-space: nowrap;
  width: 150px;
  margin: 0 auto;
  font-size: 1.4em;
  position: relative;
  overflow: visible;
}

footer li a {
  color: #fff;
  text-align: center;
  width: 100%;
  display: block;
}

footer .container .text {
  text-align: center;
  color: #fff;
  margin: 0 auto;
  font-size: .5em;
}

.container {
  margin: 0 auto;
}

.modal.show {
  z-index: 50;
  width: 100%;
  height: 100%;
  display: block;
}

.modal {
  background-color: #0000006e;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.modal_body.show {
  text-align: center;
  z-index: 51;
  background-color: #fff;
  border-radius: 10px;
  min-width: 30%;
  padding: 18px 24px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%)translateY(-50%);
  box-shadow: 0 2px 3px #22242626;
}

@keyframes fadein {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

@media screen and (width <= 800px) {
  .wrap {
    display: none;
  }

  .modal.hidden {
    z-index: 50;
    width: 100%;
    height: 100%;
    display: block;
  }

  .modal_body.hidden {
    text-align: center;
    z-index: 51;
    background-color: #fff;
    border-radius: 10px;
    width: 50%;
    height: 50%;
    padding: 40px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%)translateY(-50%);
    box-shadow: 0 2px 3px #22242626;
  }
}

.button, button {
  font-size: 1.1rem;
}

.bottom button {
  font-size: 2vw;
}

section .wrapTranslator {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

section .translator {
  color: #fdfdfde0;
  width: 35%;
  height: 50%;
  margin: 0 auto;
}

.top {
  width: 100%;
  height: 10%;
}

.mid {
  width: 100%;
  height: 35vh;
}

.btm {
  width: 100%;
  height: 10%;
}

#left {
  height: 100%;
}

#left .top {
  display: flex;
}

#left .top .text {
  color: #0a0a0aaf;
  background-color: #fff;
  width: 80%;
  padding-left: 10px;
  font-size: 1.05em;
  line-height: 2.5rem;
}

#left .top #change {
  width: 20%;
}

#left .top .button {
  white-space: nowrap;
  color: #0f0d29;
  width: 100%;
  height: 100%;
  font-size: 1.05em;
}

#left .mid {
  position: relative;
}

#left .mid form {
  width: 100%;
  height: 100%;
  display: block;
}

textarea::placeholder {
  color: #fdfdfd99;
}

#left .mid form textarea {
  box-sizing: border-box;
  resize: none;
  color: #f0f8ff;
  background-color: #ffffff65;
  border: none;
  outline: none;
  width: 100%;
  height: 100%;
  padding: 8px;
  font-family: Love Ya Like A Sister, cursive;
  font-size: larger;
  overflow: auto;
}

#left .mid #textLen {
  text-align: right;
  color: #000;
  padding: 5px;
  font-size: .5em;
  position: absolute;
  bottom: 0;
  right: 0;
}

#left .btm button {
  pointer-events: none;
  color: gray;
  width: 100%;
  padding: 10px 12px;
}

#right .top .text {
  color: #0a0a0aaf;
  background-color: #fff;
  padding-left: 10px;
  font-size: 1.05em;
  line-height: 2.5rem;
}

#right .mid .text {
  color: #000;
  background-color: #ffffff65;
  height: 100%;
  padding: 1vh 1.5vw;
  overflow: auto;
}

#right .btm {
  width: 100%;
  display: flex;
}

#right .btm #copy, #share {
  width: 100%;
}

#right .btm .button {
  width: 100%;
  padding: 10px 12px;
}

.modal_body.show #share {
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 20px 0;
  display: flex;
}

.modal_body.show #share a {
  text-align: center;
  color: #000;
  font-size: 10px;
  text-decoration-line: none;
}

.modal_body .text {
  justify-content: space-between;
  width: 100%;
  margin-bottom: 15px;
  font-size: 1.3em;
  display: flex;
}

#modal_close {
  cursor: pointer;
}
